import renderFn from '../utils/render-fn';
import {
  IconProps, SVGJson,
} from '../utils/types';
import useSizeProps from '../utils/use-size-props';

import '../style/css';

const element: SVGJson = {"tag":"svg","attrs":{"fill":"none","viewBox":"0 0 24 24","width":"1em","height":"1em"},"children":[{"tag":"g","attrs":{"id":"lemon"},"children":[{"tag":"path","attrs":{"id":"fill1","fill":"props.fillColor1","d":"M20.0001 6.50007C22.0236 9.53279 22.3232 13.677 18.0001 18.0001C13.6769 22.3232 9.53278 22.0236 6.50006 20.0001L4.59851 20.5086C3.95689 20.6194 3.39681 20.0659 3.50009 19.423L4.00006 17.5001C1.96678 14.4657 1.67048 10.3297 6.00008 6.00006C10.3297 1.67046 14.4656 1.96679 17.5001 4.00007L19.4231 3.50007C20.0659 3.39679 20.6195 3.95687 20.5086 4.59849L20.0001 6.50007Z"}},{"tag":"path","attrs":{"id":"stroke1","stroke":"props.strokeColor1","d":"M20.0001 6.50007C22.0236 9.53279 22.3232 13.677 18.0001 18.0001C13.6769 22.3232 9.53278 22.0236 6.50006 20.0001L4.59851 20.5086C3.95689 20.6194 3.39681 20.0659 3.50009 19.423L4.00006 17.5001C1.96678 14.4657 1.67048 10.3297 6.00008 6.00006C10.3297 1.67046 14.4656 1.96679 17.5001 4.00007L19.4231 3.50007C20.0659 3.39679 20.6195 3.95687 20.5086 4.59849L20.0001 6.50007Z","strokeLinecap":"square","strokeWidth":"props.strokeWidth"}},{"tag":"g","attrs":{"id":"stroke2"},"children":[{"tag":"path","attrs":{"stroke":"props.strokeColor2","d":"M8.58398 12.8299L8.58675 12.8271L8.58951 12.8299L8.58675 12.8327L8.58398 12.8299Z","strokeLinecap":"square","strokeWidth":"props.strokeWidth"}},{"tag":"path","attrs":{"stroke":"props.strokeColor2","d":"M9.29102 9.29475L9.29378 9.29199L9.29654 9.29475L9.29378 9.29752L9.29102 9.29475Z","strokeLinecap":"square","strokeWidth":"props.strokeWidth"}},{"tag":"path","attrs":{"stroke":"props.strokeColor2","d":"M12.8267 8.58772L12.8294 8.58496L12.8322 8.58772L12.8294 8.59048L12.8267 8.58772Z","strokeLinecap":"square","strokeWidth":"props.strokeWidth"}}]}]}]};

export default defineComponent({
  name: 'LemonIcon',
  props: {
    size: {
      type: String,
    },
    onClick: {
      type: Function as PropType<IconProps['onClick']>,
    },
    fillColor: {
      type: [Array, String] as PropType<IconProps['fillColor']>,
    },
    strokeColor: {
      type: [Array, String] as PropType<IconProps['strokeColor']>,
    },
    strokeWidth: {
      type: Number as PropType<IconProps['strokeWidth']>,
    }
  },
  setup(props, { attrs }) {
    const propsSize = computed(() => props.size);

    const strokeColor1 = computed(() => {
      if (!props.strokeColor) return 'currentColor';
      return Array.isArray(props.strokeColor) ? props.strokeColor[0] : props.strokeColor
    })
    const strokeColor2 = computed(() => {
      if (!props.strokeColor) return 'currentColor';
      return Array.isArray(props.strokeColor) ? props.strokeColor[1] ?? props.strokeColor[0] : props.strokeColor
    })

    const fillColor1 = computed(() => {
      if (!props.fillColor) return 'transparent';
      return Array.isArray(props.fillColor) ? props.fillColor[0] : props.fillColor
    })
    const fillColor2 = computed(() => {
      if (!props.fillColor) return 'transparent';
      return Array.isArray(props.fillColor) ? props.fillColor[1] ?? props.fillColor[0] : props.fillColor
    })

    // 填充类型图标
    const filledColor = computed(() => {
      if (!props.fillColor) return 'currentColor';
      return Array.isArray(props.fillColor) ? props.fillColor[0] : props.fillColor;
    })


    const { className, style } = useSizeProps(propsSize);

    const finalCls = computed(() => ['t-icon', 't-icon-lemon', className.value]);
    // fill none 是为了避免存在旧版本图标的 fill:currentColor 造成的样式污染
    const finalStyle = computed(() => ({ fill: 'none', ...style.value, ...(attrs.style as Styles) }));
    const finalProps = computed(() => ({
      class: finalCls.value,
      style: finalStyle.value,
      onClick: (e: MouseEvent) => props.onClick?.({ e }),
      strokeColor1: strokeColor1.value,
      strokeColor2: strokeColor2.value,
      fillColor1: fillColor1.value,
      fillColor2: fillColor2.value,
      strokeWidth: props.strokeWidth || 2,
      filledColor: filledColor.value
    }));
    return () => renderFn(element, finalProps.value);
  },

});
